import React,  { useState, useEffect } from "react"
import { Form, Button, Row, Col } from "react-bootstrap"
import { Link, useLocation, useNavigate } from "react-router-dom"
import { useDispatch, useSelector } from "react-redux"
import Loader from "../components/Loader"
import Message from "../components/Message"
import { register } from "../actions/userActions"
import FormContainer from "../components/FormContainer"


const RegisterScreen = () => {

    const [name, setName] =  useState('')
    const [email, setEmail] =  useState('')
    const [confirmPassword, setConfirmPassword] =  useState('')
    const [password, setPassword] =  useState('')
    const [message, setMessage] =  useState('')


    const dispatch = useDispatch()
    const navigate = useNavigate()
    const location = useLocation()
    const redirect = new URLSearchParams(location.search).get("redirect");
    

    const userRegister = useSelector(state => state.userRegister)
    const { error, loading, userInfo } = userRegister

    

    useEffect(() => {
        if(userInfo) {
            navigate(redirect || "/")
        }

    },[navigate, userInfo, redirect])
    const submitHandler = (e) => {
        e.preventDefault()
        if (password != confirmPassword) {
          setMessage("Passwords do not match")
        }
        else{
          dispatch(register(name, email, password))
        }
        
        
    }

  return (
    <FormContainer>
        <h1>Sign Up</h1>
        {message &&  <Message variant='danger'>{message}</Message>}
        {error && <Message variant="danger">{error}</Message>}
        {loading && <Loader />}
        <Form onSubmit={submitHandler}>
        <Form.Group controlId="name" className="mb-3">
                <Form.Label className="mb-3">
                    Name

                </Form.Label>
                <Form.Control required type="name" placeholder="Enter Name"
                value={name} onChange={(e) => setName(e.target.value) } 
                style={{ backgroundColor: '#DAEAF6' }}/>

                

            </Form.Group>
        <Form.Group controlId="email" className="mb-3">
                <Form.Label className="mb-3">
                    Email Address

                </Form.Label>
                <Form.Control required type="email" placeholder="Enter Email"
                value={email} onChange={(e) => setEmail(e.target.value) } 
                style={{ backgroundColor: '#DAEAF6' }}/>

                

            </Form.Group>
            <Form.Group controlId="password" className="mb-3">
                <Form.Label className="mb-3">
                    Password

                </Form.Label>
                <Form.Control required type="password" placeholder="Enter Password"
                value={password} onChange={(e) => setPassword(e.target.value) } 
                style={{ backgroundColor: '#DAEAF6' }}/>

                

            </Form.Group>
            <Form.Group controlId="confirmpassword" className="mb-3">
                <Form.Label className="mb-3">
                    Confirm Password

                </Form.Label>
                <Form.Control required type="password" placeholder="Confirm Password"
                value={confirmPassword} onChange={(e) => setConfirmPassword(e.target.value) } 
                style={{ backgroundColor: '#DAEAF6' }}/>

                

            </Form.Group>
            <Button type="submit" value={"hi"} variant="primary">
                Sign In
            </Button>
        </Form>
        <Row className="py-3">
            <Col>
            Already have an Account ? <Link to={userInfo ? "/" : redirect ? `/login?redirect=${redirect}` : "/login"}>
                Sign In
            </Link>
            </Col>
        </Row>
    </FormContainer>
  )
}

export default RegisterScreen