import React,  { useState, useEffect } from "react"
import { Form, Button } from "react-bootstrap"
import { Link, useLocation, useNavigate } from "react-router-dom"
import { useDispatch, useSelector } from "react-redux"
import FormContainer from "../components/FormContainer"
import { saveShippingAddress } from "../actions/cartActions"
import CheckoutSteps from "../components/CheckoutSteps"



const ShippingScreen = () => {
  const cart = useSelector((state) => state.cart)
  const { shippingAddress } = cart;
  const navigate = useNavigate()

  const dispatch = useDispatch()

  const [address, setAddress] =  useState(shippingAddress?.address || "")
  const [city, setCity] =  useState(shippingAddress?.city || "")
  const [postalCode, setPostalCode] =  useState(shippingAddress?.postalCode || "")
  const [country, setCountry] =  useState(shippingAddress?.country || "")

  const submitHandler = (e) => {
    e.preventDefault()
    dispatch(saveShippingAddress({ address, city, postalCode, country }))
    navigate("/payment")
  }
  
  return (
    <FormContainer>
      <CheckoutSteps step1 step2 />
      <h1>Shipping</h1>
      <Form onSubmit={submitHandler}>

      <Form.Group controlId="address" className="mb-3">
                <Form.Label className="mb-3">
                     Address

                </Form.Label>
                <Form.Control required type="text" placeholder="Enter Your address "
                value={address ? address : ""} onChange={(e) => setAddress(e.target.value) } 
                style={{ backgroundColor: '#DAEAF6' }}/>

                

            </Form.Group>

            <Form.Group controlId="city" className="mb-3">
                <Form.Label className="mb-3">
                    City

                </Form.Label>
                <Form.Control required type="text" placeholder="Enter your City"
                value={city ? city : ""} onChange={(e) => setCity(e.target.value) } 
                style={{ backgroundColor: '#DAEAF6' }}/>

                

            </Form.Group>
            <Form.Group controlId="postalCode" className="mb-3">
                <Form.Label className="mb-3">
                    Postal Code

                </Form.Label>
                <Form.Control required type="text" placeholder="Enter your Postal Code"
                value={postalCode ? postalCode : ""} onChange={(e) => setPostalCode(e.target.value) } 
                style={{ backgroundColor: '#DAEAF6' }}/>

                

            </Form.Group>
            <Form.Group controlId="country" className="mb-3">
                <Form.Label className="mb-3">
                    Country

                </Form.Label>
                <Form.Control required type="text" placeholder="Enter your Country"
                value={country ? country : ""} onChange={(e) => setCountry(e.target.value) } 
                style={{ backgroundColor: '#DAEAF6' }}/>

                

            </Form.Group>
            <Button type="submit" variant="primary"> Continue</Button>

      </Form>
    </FormContainer>
  )
}

export default ShippingScreen