import React, { useState } from 'react'
import { Button, Form, Nav } from 'react-bootstrap'
import { useHistory, useLocation, useNavigate } from 'react-router-dom'

function SearchBox() {
    const [keyword, setKeyword] = useState('')


    const navigate = useNavigate()
    const location = useLocation()

    const submitHandler = (e) => {
        e.preventDefault()
        if (keyword) {
            navigate(`/?keyword=${keyword}&page=1`)
        } else {
            navigate(location.pathname);
        }
    }
    return (
        <Nav className="me-auto">
        <Form  className="d-flex mx-3 mx-lg-0 mt-4 mt-md-0 mb-4 mb-md-0 " onSubmit={submitHandler} inline>
          <Form.Control name='q' onChange={(e) => setKeyword(e.target.value)} className="" style={{ color: 'black', height: '38px', marginRight: '10px' }} type="search" placeholder="Search" />
          <Button type='submit' variant="secondary" className="d-flex align-items-center" style={{ height: '38px' }}>
            Search
          </Button>
        </Form>
      </Nav>
    )
}

export default SearchBox
