import React from 'react';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faStar } from '@fortawesome/free-solid-svg-icons';
import {  faStarHalfStroke } from '@fortawesome/free-regular-svg-icons';
import { faStar as regularStar } from '@fortawesome/free-regular-svg-icons';


const Rating = ({ value, text, color }) => {
  const fullStars = Math.floor(value);
  const hasHalfStar = value - fullStars >= 0.5;

  const stars = [];
  for (let i = 0; i < 5; i++) {
    if (i < fullStars) {
      stars.push(<FontAwesomeIcon key={i} icon={faStar}  style={{ color}} />);
    } else if (i === fullStars && hasHalfStar) { 
      stars.push(<FontAwesomeIcon key={i} icon={faStarHalfStroke}  style={{ color }} />);
    } else {
      stars.push(<FontAwesomeIcon key={i} icon={regularStar} style={{ color }} />);
    }
  }

  return (
    <div className='rating'>
      <span>{stars}</span>
      <span style={{margin : '10px'}}>{ text && text }</span>
    </div>
  );
};

export default Rating;
