import React, { useState } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCartShopping, faUser } from '@fortawesome/free-solid-svg-icons';
import { Link } from 'react-router-dom';
import { LinkContainer } from 'react-router-bootstrap'
import { Navbar, Nav, Button, Form, NavDropdown } from 'react-bootstrap'; 
import { useDispatch, useSelector } from 'react-redux';
import { logout } from '../actions/userActions';
import SearchBox from './SearchBox';

const Header = () => {
  const userLogin = useSelector(state => state.userLogin)
  const { userInfo } = userLogin
  const [isDropdownOpen, setIsDropdownOpen] = useState(false);
  const dispatch = useDispatch()

  const toggleDropdown = () => {
    setIsDropdownOpen(!isDropdownOpen);
  };

  const logoutHandler = () => {
    dispatch(logout)

  }

  return (
    <div>
      <header>
     
        <Navbar bg="primary" variant="dark" expand="lg" className="justify-content-between">
          <Navbar.Brand as={Link} to="/" className='mx-4'  >SparkEase</Navbar.Brand>
          <Navbar.Toggle style={{boxShadow: 'none'}}  aria-controls="navbarColor01" onClick={toggleDropdown} className='mx-3' />
          <Navbar.Collapse id="navbarColor01" className={`justify-content-md-end ${isDropdownOpen ? 'show' : ''}`}>
            
            <SearchBox />
            <Nav>
              <Nav.Item>
                <Nav.Link as={Link} to="/cart" className='mx-4 mx-lg-0'>
                  <FontAwesomeIcon icon={faCartShopping} />
                  <span className="p-2">Cart</span>
                </Nav.Link>
              </Nav.Item>
              {userInfo ? (
                
                <NavDropdown title={userInfo.name} id='username'>
                <LinkContainer  to="/profile">
                    <NavDropdown.Item>Profile</NavDropdown.Item>
                </LinkContainer>

                <NavDropdown.Item onClick={logoutHandler}>Logout</NavDropdown.Item>

            </NavDropdown>) : (
                <Nav.Item>
                <Nav.Link as={Link} to="/login" className='mx-4 mx-lg-0'>
                  <FontAwesomeIcon icon={faUser} />
                  <span className="p-2">Login</span>
                </Nav.Link>
              </Nav.Item>) }
              
            </Nav>
          </Navbar.Collapse>
        </Navbar>
    
      </header>
    </div>
  );
};

export default Header;
