import React, { useEffect } from "react";
import { Link, useParams, useLocation, useNavigate } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faTrash } from '@fortawesome/free-solid-svg-icons';
import {
  Row,
  Col,
  ListGroup,
  Image,
  Form,
  Button,
  Card,
} from "react-bootstrap";
import { addToCart, removeFromCart } from "../actions/cartActions";
import Message from "../components/Message";

const CartScreen = () => {
  const { id } = useParams();
  const location = useLocation();
  const navigate = useNavigate();
  const userInfo = useSelector((state) => state.userLogin.userInfo);
  const productId = id;
  const qty = new URLSearchParams(location.search).get("qty");
 
  const dispatch = useDispatch();

  const cart = useSelector((state) => state.cart);
  const { cartItems } = cart;

  useEffect(() => {
    if (productId) {
      dispatch(addToCart(productId, qty));
    }
  }, [dispatch, productId, qty]);

  const removeFromCartHandler = (id) => {
    dispatch(removeFromCart(id))
    

  }
  const checkoutHandler = () => {
    navigate(`/login?redirect=shipping`);
  }
  return (
    <Row>
      <Col md={8}>
        <h1>Shopping Cart</h1>
        {cartItems.length === 0 ? (
          <Message variant="info">
            Your cart is empty <Link to="/">Go Back</Link>
          </Message>
        ) : (
          <ListGroup variant="flush">
            {cartItems.map((item) => (
              
              <ListGroup.Item key={item.product}>
                <Row>
                  <Col md={2} xs={12} className="px-2 py-2">
                    <Image src={item.image} alt={item.name} fluid rounded />
                  </Col>
                  <Col md={3} xs={12} className="px-2 py-2">
                    <Link className='card-link' to={`/product/${item.product}`}>{item.name}</Link>
                  </Col>
                  <Col md={2} xs={12} className="px-2 py-2">₹{item.price}</Col>
                  <Col md={3} xs={12} className="px-2 py-2">
                  <Form.Select
                             as="select"
                            value={item.qty}
                            onChange={(e) => dispatch(addToCart(item.product, Number(e.target.value)))}>
                              {
                                [...Array(item.countInStock).keys()].map((x) => (
                                  <option key={x + 1} value={x + 1}>{x + 1}</option>
                                ))
                              }

                            </Form.Select>
                  </Col>
                  <Col md={1}>
                    <Button   type="button" variant="light" onClick={() => removeFromCartHandler(item.product) }>
                    <FontAwesomeIcon icon={faTrash} />

                    </Button>
                  </Col>

                </Row>
              </ListGroup.Item>
            ))}
          </ListGroup>
        )}
      </Col>
      <Col>
      <Card md={4}>
        <ListGroup variant="flush">
          <ListGroup.Item>
            <h3>Subtotal ({cartItems.reduce((acc, item) => acc + item.qty, 0  )})</h3>
            ₹{cartItems.reduce((acc, item) => acc + item.qty * item.price, 0).toFixed(2)}
          </ListGroup.Item>
          <ListGroup.Item>
          <Button type="button"
          className="btn-block w-100" disabled={cartItems.length === 0}
          onClick={checkoutHandler}>
            Proceed to checkout

          </Button>
        </ListGroup.Item>
        </ListGroup>
       
      </Card>
      </Col>
    </Row>
  );
};

export default CartScreen;
